import React, { Component } from 'react';

import userPlaceholder from '../sources/images/user-placeholder.jpg';

import { API } from '../const/newsApi';  

class NewsFetch extends Component {
  constructor(props) {
    super(props);

    this.state = {
      articles: [],
      isLoading: false,
    };
  }

  componentDidMount() {
    this.setState({ isLoading: true });

    fetch(API)
      .then(response => response.json())
      .then(data => this.setState({ articles: data.articles, isLoading: false }));

  }


  render() {
    const { articles, isLoading } = this.state;

    if (isLoading) {
      return <p>Loading ...</p>;
    }
    
    return (
      <div>
        {articles.map(item =>
            <div key={item.id} className="news-content">
              <a href={item.url}>
                <img src={item.urlToImage} className="main-img" alt="cover"></img>
                <h1>{item.title}</h1>
              </a>
              <div className="author-wrap">
                <img src={userPlaceholder} alt="user-placeholder"></img>
                <span>by {item.author || 'Unknown author'}</span>
              </div>
              <p className="description">{item.description}</p>
            </div>
        )}
      </div>
    );
  }
}

export default NewsFetch;

