import React, { Component } from 'react';

//Import Bootstrap Components
import {
  Collapse,
  Navbar,
  Nav,
  NavItem,
  NavLink,
  Container } from 'reactstrap';

import { BrowserRouter as Router, Route, Link } from "react-router-dom";

//Import Components
import Logo from './components/logo';
import MenuMobileIcon from './sources/images/menu@2x.png';  
import newsCategories from './const/newsCategories';

//Views
import Home from './components/home';
import Login from './components/login';

class App extends Component {
  constructor(props) {
    super(props);

    this.toggleNavbar = this.toggleNavbar.bind(this);
    this.state = {
      collapsed: true
    };
  }

  toggleNavbar() {
    this.setState({
      collapsed: !this.state.collapsed
    });
  }

  render() {
    return (
      <div>
        <Router>
          <header>
          <Container>
            <Logo></Logo>
              <div className="menuList">
                <div className="hidden-sm">
                  <Navbar className="main-nav">
                    <Nav>
                      {newsCategories.map(item =>
                        <NavItem>
                          <NavLink className={item.class}>{item.name}</NavLink>
                        </NavItem>
                      )}  
                        <NavItem>
                          <NavLink className="login"><Link to="/login/">Login</Link></NavLink>
                        </NavItem>
                    </Nav>
                  </Navbar>
                </div>
                <div className="visible-sm-only">
                  <button onClick={this.toggleNavbar} className="mobile-toggle">
                        <img src={MenuMobileIcon} alt="hamburger-icon"></img>
                  </button>
                  <Navbar className="main-nav main-nav--mobile">
                    <Collapse isOpen={!this.state.collapsed} navbar>
                    <Nav>
                      {newsCategories.map(item =>
                        <NavItem>
                          <NavLink className={item.class}>{item.name}</NavLink>
                        </NavItem>
                      )}  
                        <NavItem>
                          <NavLink className="login">Login</NavLink>
                        </NavItem>
                    </Nav>
                    </Collapse>
                  </Navbar>
                </div>
              </div>
          </Container>
        </header>
        <Container>
          <Home></Home>
          <Route exact path='/' component={Home}></Route>
          <Route exact path='/login' component={Login}></Route>
        </Container>
      </Router>
      </div>   
    );
  }
}

export default App;
