import React, { Component } from 'react';

import userPlaceholder from '../sources/images/user-placeholder.jpg';

import { API } from '../const/newsApi';  

class NewsFetchSlide extends Component {
  constructor(props) {
    super(props);

    this.state = {
      articles: [],
      isLoading: false,
    };
  }

  componentDidMount() {
    this.setState({ isLoading: true });

    fetch(API)
      .then(response => response.json())
      .then(data => this.setState({ articles: data.articles, isLoading: false }));

  }


  render() {
    const { articles, isLoading } = this.state;

    if (isLoading) {
      return <p>Loading ...</p>;
    }
    
    //function for slides
    var current = 0,
    slides = document.getElementsByClassName('news-content-slide');
    setInterval(function() {
      for (var i = 0; i < slides.length; i++) {
        slides[i].classList.remove('show');
      }
      current = (current !== slides.length - 1) ? current + 1 : 0;
      slides[current].classList.add('show');
    }, 10000);

    return (
      <div>
        {articles.map(item =>
            <div key={item.id} className="news-content news-content-slide">
              <a href={item.url}>
                <img src={item.urlToImage} className="main-img" alt="cover"></img>
                <h1>{item.title}</h1>
              </a>
              <div className="author-wrap">
                <img src={userPlaceholder} alt="user-placeholder"></img>
                <span>by {item.author || 'Unknown author'}</span>
              </div>
              <p className="description">{item.description}</p>
            </div>
        )}
      </div>
    );
  }
}

export default NewsFetchSlide;

