import React, { Component } from 'react';
//Import Bootstrap Components
import {
    Container,
    Row,
    Col } from 'reactstrap';

//Import Components
import NewsFetch from './news';
import NewsFetchSlide from './newsSlide';

import { newsCategories } from '../const/newsCategories'; 
const idPolitics = newsCategories.filter(item => item.id === 1);
const idBusiness = newsCategories.filter(item => item.id === 2);
const idTech = newsCategories.filter(item => item.id === 3);
const idScience = newsCategories.filter(item => item.id === 4);
const idSports = newsCategories.filter(item => item.id === 5);

class Home extends Component {

  render() {
    return (
        <Container >
        <Row className="news-feed" data="default">
          <Col className="col-md-6 col-xs-12 main-category">
          <div className="news-block">
          {idPolitics.map(item =>
            <span className={'news-category ' + item.class}>{item.name}</span>
          )}
            <NewsFetchSlide ></NewsFetchSlide>
          </div>
          </Col>
          <Col className="col-md-3 col-xs-6 secondary-category">
            <div className="news-block">
            {idTech.map(item =>
              <span className={'news-category ' + item.class}>{item.name}</span>
            )}
              <NewsFetch ></NewsFetch>
            </div>
          </Col>
          <Col className="col-md-3 col-xs-6 secondary-category">
            <div className="news-block">
              {idScience.map(item =>
                <span className={'news-category ' + item.class}>{item.name}</span>
              )}
                <NewsFetch ></NewsFetch>
              </div>
          </Col>
        </Row >
        <Row className="separator"></Row>
        <Row className="news-feed" data="extra">
          <Col className="col-md-4 col-sm-6 col-xs-6">
            <div className="news-block">
              {idBusiness.map(item =>
                <span className={'news-category ' + item.class}>{item.name}</span>
              )}
                <NewsFetch ></NewsFetch>
            </div>
          </Col>
          <Col className="col-md-4 col-sm-6 col-xs-6">
            <div className="news-block">
              {idSports.map(item =>
                <span className={'news-category ' + item.class}>{item.name}</span>
              )}
                <NewsFetch ></NewsFetch>
            </div>
          </Col>
          <Col className="col-md-4 col-sm-6 col-xs-6">
            <div className="news-block">
              {idScience.map(item =>
                <span className={'news-category ' + item.class}>{item.name}</span>
              )}
                <NewsFetch ></NewsFetch>
            </div>
          </Col>
        </Row>
        </Container>
    );
  }
}

export default Home;

